import axios from 'axios';

import { Message, Loading} from 'element-ui';
// //config
// const testUrl = 'http://123.127.237.106:6379';
const testUrl = 'http://anticovid19.noprod-b.kmt.orange.com'
const devrUrl = 'http://localhost:9999';

let url = process.env.NODE_ENV =='development' ? devrUrl  : testUrl;
console.log(url)
const prodUrl = '';
const MSG_TIME = 2*1000;


const setLoading = (optObj= {})=>{
        let loadingCfg = Object.assign({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            // background: 'rgba(0, 0, 0, 0.7)'
        },optObj)
        Loading(loadingCfg)
}

const setMsg = (optObj={})=>{
    let cfg = Object.assign({
        message: 'success!',
        type:'success',// success/warning/info/error  info is default
        center:true,
        duration: MSG_TIME, //3000 
    },optObj)
    Message(cfg);
}

//base ajax config 
const instance = axios.create({
    baseURL: url,
    timeout: 3000
});

//befoe reqeust
instance.interceptors.request.use(
    config=>{
        return config // must return or will error  ::  Cannot read property 'cancelToken' of undefined
    },
    err=>{
       return Promise.reject(err)
    }
)

//after response

instance.interceptors.response.use(
    config=>{

        if(config && config.status == 200 && config.data.flag == 1){
            //只有getlist 才组装数据
            if(config.data.data.result && config.data.data.result.length>0){
                setMsg();
                let res = config.data.data
                return res;
            }
            return config
        }else{
            setMsg({
                message:  config.data.message || 'fail',
                type:'error'
            })
           
        }
        return config //https://github.com/svrcekmichal/redux-axios-middleware/issues/83
    },
    err=>{
        Promise.reject(err)
    }
)

export default instance