<template>
    <el-form :inline="true" :model="formInline" class="demo-form-inline p20">
    <el-form-item label="code ">
        <el-input v-model="formInline.code" placeholder="please input code "></el-input>
    </el-form-item>
    <el-form-item>
        <el-button type="primary" @click="onSubmit">submit</el-button>
    </el-form-item>
    </el-form>
</template>
<script>
export default {
    data(){
        return{
            formInline:{
                 code:''
            }
        }
    },
    methods:{
        onSubmit(){
            if(!this.formInline.code){
                this.$message({
                     message: 'please input code!',
                     type:'error',// success/warning/info/error  info is default
                     center:true,
                })
                return
            }
            let cfg = {
                verifyCode : this.formInline.code
            }
            this.$http.post('/admin/updatePatientToConfirm',cfg)
                      .then(res=>{
                          if(res && res.data && res.data.flag ==1){
                              this.formInline.code = ''
                              this.$message({
                                message: 'update patient to confirm !',
                                type:'success',// success/warning/info/error  info is default
                                center:true,
                              })
                          }
                      })
                      .catch(err=>{
                          console.log(err)
                      })
        }
    }
}
</script>
<style scoped>
    .p20{
        padding: 20%
    }
</style>